* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
textarea {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  font-size: 15px;
  line-height: 18px;
  color: #1c1417;
  background: rgb(251, 197, 49);
}

a {
  color: #1c1417;
  text-decoration: none;
}
.header-img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: bottom;
}

.header-img--error {
  object-fit: contain;
}
.nav {
  display: flex;
  list-style: none;
  overflow-x: auto;
  overflow-y: hidden;
}

.nav-elem:not(:nth-of-type(1)) {
  margin-left: 20px;
}

.nav-link {
  position: relative;
  display: block;
  padding: 5px 30px 15px;
  transition: .2s ease;
}

.nav-link::before {
  content: '';
  position: absolute;
  left: 50%;
  bottom: -20px;
  transform: translateX(-50%) rotate(45deg);
  display: block;
  width: 15px;
  height: 15px;
  background: #ffffff;
  transition: .2s ease;
}

.nav-link:hover,
.nav-link--active {
  transform: scale(1.1);
  transition: .2s ease;
}

.nav-link:hover::before,
.nav-link--active::before {
  bottom: -8px;
  transition: .2s ease;
}

.article-list {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 50px;
  margin: 50px auto;
}

.article {
  overflow: hidden;
}

.article-title {
  position: relative;
  font-size: 28px;
  line-height: 30px;
  letter-spacing: -.5px;
  margin-bottom: 3px;
  cursor: pointer;
}

.article-title:before {
  content: '';
  position: absolute;
  left: -100%;
  bottom: 0;
  display: block;
  height: 1px;
  width: 100%;
  background: #1c1417;
  transition: .5s ease;
}

.article-title:hover:before {
  left: 0;
  transition: .25s ease;
}

.article-text {
  font-size: 15px;
  line-height: 20px;
  letter-spacing: .3px;
}

.article-date {
  font-size: 12px;
  line-height: 14px;
  margin-bottom: 15px;
}
.contact-form {
display: flex;
flex-direction: column;
}

.contact-input {
  font-size: 15px;
  line-height: 20px;
  letter-spacing: .3px;
  display: block;
  width: 100%!important;
  min-height: 200px;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 5px;
  outline: none;
  resize: none;
}

.contact-button {
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  padding: 10px 50px;
  cursor: pointer;
  font-size: 15px;
  line-height: 18px;
  letter-spacing: .5px;
  text-transform: uppercase;
  background: transparent;
  border: 1px solid #1c1417;
  border-radius: 5px;
  transition: .25s ease;
}

.contact-button:hover {
  background: rgb(251, 197, 49);
  border-color: rgb(251, 197, 49);
  transition: .25s ease;
}

.contact-button[disabled] {
  border-color: #1c1417;
  color: #1c1417;
  opacity: .2;
  cursor: auto;
}

.contact-button[disabled]:hover {
  background: transparent;

}
.contact {
  flex: 1 1;
  width: 100%;
}

.contact-title {
  font-size: 30px;
  line-height: 40px;
  letter-spacing: -.5px;
  margin-bottom: 30px;
}
.login-form {}

.login-label {
  display: block;
  font-size: 15px;
  font-weight: bold;
  line-height: 20px;
  letter-spacing: .3px;
  margin-bottom: 20px;
}

.login-input {
  font-size: 15px;
  line-height: 20px;
  letter-spacing: .3px;
  display: block;
  width: 100%;
  padding: 5px 10px;
  margin-top: 5px;
  border: 1px solid #1c1417; 
  border-radius: 5px;
  outline: none;
}

.login-button {
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  padding: 10px 50px;
  cursor: pointer;
  font-size: 15px;
  line-height: 18px;
  letter-spacing: .5px;
  text-transform: uppercase;
  background: transparent;
  border: 1px solid #1c1417;
  border-radius: 5px;
  transition: .25s ease;
}

.login-button:hover {
  background: rgb(251, 197, 49);
  border-color: rgb(251, 197, 49);
  transition: .25s ease;
}

.login-button[disabled] {
  border-color: #1c1417;
  color: #1c1417;
  opacity: .2;
  cursor: auto;
}

.login-button[disabled]:hover {
  background: transparent;

}
.login {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 300px;
  margin: 30px auto;
}
.container {
  display: flex;
  flex-direction: column;
  max-width: 960px;
  min-height: calc(100vh - 15px);
  margin: 15px auto 0;
}

@media only screen and (max-width: 990px) {
  .container {
    margin: 15px 15px 0;
  }
}

header {
  height: 300px;
}

main {
  flex: 1 1;
  display: flex;
  flex-direction: column;
}

nav {
  padding: 10px 0 0;
  background: rgb(251, 197, 49);
  overflow: hidden;
}

section {
  flex: 1 1;
  display: flex;
  padding: 10px 15px;
  border-radius: 5px;
  background: #ffffff;
}

footer {
  padding: 10px 0;
  background: rgb(251, 197, 49);
}
